.about-container {
  display: flex;
  justify-content: space-between;
  /* border: 1px solid red; */
  padding: 60px 0;
}
.about-container > img {
  width: 40%;
  height: auto;
}

.about-container > ul {
  display: flex;
  flex-direction: column;
  gap: 25px;
  line-height: 23px;
  list-style: circle;
  font-size: 17px;
  color: black;
  font-weight: 400;
  font-style: italic;
  width: 50%;
  /* border: 1px solid blue; */
}
@media screen and (max-width: 1000px) {
  .about-container > img {
    display: none;
  }

  .about-container > ul {
    margin-top: 20px;

    width: 100%;
  }
}
@media screen and (max-width: 500px) {
  .about-container > img {
    display: none;
  }

  .about-container > ul {
    margin-top: 20px;
    width: 100%;
  }
}
