.skills-container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 25px;
}
@media screen and (min-width: 400px) and (max-width: 1000px) {
  .skills-container {
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }
}
@media screen and (max-width: 500px) {
  .skills-container {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
}
