.skill-card {
  background-color: rgb(245, 224, 228);
  display: flex;
  justify-content: center;
  /* border: 1px solid red; */
  padding: 20px;
  transition: 1s;
  border-radius: 10px;
}
.skill-card:hover {
  transform: scale(1.1);
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 4px 12px;
}
@media screen and (min-width: 500px) and (max-width: 1000px) {
  .skill-card {
    width: 100%;
  }
  .skill-card > img {
    /* width: 100; */
    height: auto;
  }
}
@media screen and (max-width: 500px) {
  .skill-card {
    width: 100%;
  }
  .skill-card > img {
    /* width: 100px; */
    height: auto;
  }
}
